import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Collapse from '@material-ui/core/Collapse';
import MainFoodList from './MainFoodList';

const useStyles = makeStyles(theme => ({
  root: {
    // height: 'calc(100vh - 60px)',
    position: 'absolute',
    width: '100vw',
    height: '100vh',
    top: 0,
    display: 'flex',
    justifyContent: 'center',
    zIndex: 10,
    backgroundColor: props => (props ? 'rgba(0, 0, 0, 0.32)' : 'transparent'),
    paddingTop: 50,
    transition: 'all 0.2s ease',
  },
  paper: {
    margin: theme.spacing(1),
    borderRadius: 4,
  },
}));

export default function SearchResult({ visible, list }) {
  const classes = useStyles(visible);
  return (
    <div className={classes.root}>
      <Collapse in={visible} collapsedHeight="0">
        <Paper elevation={4} className={classes.paper}>
          <MainFoodList list={list} />
        </Paper>
      </Collapse>
    </div>
  );
}
